.nowtime{
  width: 392px;
  height: 207px;
  .title{
    width: 137px;
    height: 37px;
    text-align: center;
    line-height: 37px;
    font-size: 16px;
    letter-spacing: 1px;
    color: #ffffff;
  }
  .kuang{
    margin-top: 13px;
    width: 392px;
    height: 157px;
    background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%, );
    ul{
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      li{
        float: left;
        list-style: none;
        width: 50%;
        height: 50%;
        
        span{
          float: left;
          display: inline-block;
          width: 27px;
          height: 27px;
          margin: 30px 0 0 20px;
        }
        .number{
          margin-left: 17px;
          float: left;
          width: 60%;
          height: 78%;
          margin-top: 10px;
          p{
            margin-top: 10px;
            font-size: 13px;
            color: #ffffff;
            margin: 0;
            i{
              font-style: normal;
              font-weight: 700;
              font-size: 25px;
              letter-spacing: 1px;
	            color: #41b8f9;
            }
          }
          .zongshu{
            font-style: 15px;
            color: #ffffff;
          }
        }
      }
    }
  }
}